@use '../abstracts/' as *;

.header {
    --pos: relative;
    background: plum;
    position: var(--pos);

    .brand-logo {
        background: linear-gradient(royalblue, plum, orange,);
    }

    img {
        @include square(var(--size-7));
        object-fit: contain;
        aspect-ratio: 1/1;
        overflow: hidden;
    }
}

.header[data-type="sticky"] {
    --pos: sticky;
    top: 0;
}

.header__inner {
    @include flex-base();
    justify-content: space-between;
    padding-block: var(--size-2);

    @include breakpoint(tablet) {
        padding: 0;
    }
}